.search-bar:hover {
  background-color: #d8cececc;
  cursor: pointer;
}


.search-tab-list ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.form-search[tabindex] {
  outline: none;
  position: relative;
}



